import {
  Component,
  OnInit
} from '@angular/core';
import {
  AlertController
} from '@ionic/angular';
import {
  Ranges
} from 'src/models/';

import {
  DataProvider,
  LocalDatabaseProvider
} from './../../providers/';


@Component({
  selector: 'app-consumption-ranges',
  templateUrl: './consumption-ranges.page.html',
  styleUrls: ['./consumption-ranges.page.scss'],
})
export class ConsumptionRangesPage implements OnInit {

  ranges: Ranges = {
    good: 0.50,
    mediocre: 2.00,
    needsImprovement: 2.00,
    eventNotificationLimit: 'needs improvement',
    weeklyLimit: 15,
    monthlyLimit: 15,
  };

  constructor(
    public alertController: AlertController,
    public data: DataProvider,
    public localDb: LocalDatabaseProvider
  ) {}

  ngOnInit() {

  }

  getCurrencySymbol() {
    return this.data.user.units.currency === 'eur' ? '€' : '$';
  }


  async presentRangePricePrompt(rangePriceType: string) {
    // const header = this.units.currency === 'eur' ? 'Water price € / m³' : 'Water price cents / gallon';
    // const value = this.units.currency === 'eur' ? this.units.waterPrice : this.units.waterPrice * 100;
    let message;
    let value;
    let min;
    let max;
    if (rangePriceType === 'good') {
      message = `Show <b>Good</b> notification when total cost is less than ${this.getCurrencySymbol()}${this.data.user.ranges.good}`;
      value = this.data.user.ranges.good;
      min = 0;
      max = this.data.user.ranges.mediocre;
    }
    if (rangePriceType === 'mediocre') {
      message = `Show <b>Mediocre</b> notification when total cost is less than ${this.getCurrencySymbol()}${this.data.user.ranges.mediocre}`;
      value = this.data.user.ranges.mediocre;
      min = this.data.user.ranges.good;
      // max = this.data.user.ranges.needsImprovement;
      max = 1000000;
    }
    if (rangePriceType === 'needsImprovement') {
      message = `Show <b>Needs improvement</b> notification when total cost is more than ${this.getCurrencySymbol()}${this.data.user.ranges.needsImprovement}`;
      value = this.data.user.ranges.needsImprovement;
      min = this.data.user.ranges.good;
      // min = this.data.user.ranges.mediocre;
      max = 1000000;
    }
    const alert = await this.alertController.create({

      header: 'Cost range',
      message: message,
      inputs: [{
        name: 'price',
        type: 'number',
        value: value ? value : '',
      }],
      buttons: [{
        text: 'Cancel',
        role: 'cancel',
        cssClass: 'secondary',
        handler: () => {
        }
      }, {
        text: 'Ok',
        handler: (data) => {
          if (max >= data.price && data.price <= min) {
            this.presentInvalidRangesAlert(rangePriceType);
          } else {
            if (rangePriceType === 'good') {
              this.data.user.ranges.good = data.price;
              this.localDb.updateUser({
                ...this.data.user
              });
            }
            if (rangePriceType === 'mediocre') {
              this.data.user.ranges.mediocre = data.price;
              this.data.user.ranges.needsImprovement = data.price;
              this.localDb.updateUser({
                ...this.data.user
              });
            }
            if (rangePriceType === 'needsImprovement') {
              this.data.user.ranges.mediocre = data.price;
              this.data.user.ranges.needsImprovement = data.price;
              this.localDb.updateUser({
                ...this.data.user
              });
            }
          }
        }
      }]
    });
    await alert.present();
  }

  async presentInvalidRangesAlert(invalidValue: string) {
    let message: string;
    if (invalidValue === 'good') {
      message = 'Good must be smaller than Mediocre and Needs Improvement';
    }
    if (invalidValue === 'mediocre') {
      message = 'Mediocre and Needs Improvement must be larger than Good';
    }
    if (invalidValue === 'needsImprovement') {
      message = 'Needs Improvement and Mediocre must be larger than Good';
    }

    const alert = await this.alertController.create({
      header: 'Cost ranges',
      message: message,
      buttons: [{
        text: 'Ok',
        handler: (data) => {
        }
      }]
    });
    await alert.present();
  }

  async presentNotificationEventLimitRadio() {
    const alert = await this.alertController.create({
      header: 'Show notification when event exceeds',
      inputs: [{
          name: 'no',
          type: 'radio',
          label: 'No notification',
          value: 'no',
          checked: this.data.user.ranges.eventNotificationLimit === 'no notification' ? true : false
        },
        {
          name: 'mediocre',
          type: 'radio',
          label: 'Mediocre',
          value: 'mediocre',
          checked: this.data.user.ranges.eventNotificationLimit === 'mediocre' ? true : false
        },
        {
          name: 'needs',
          type: 'radio',
          label: 'Needs improvement',
          value: 'needs',
          checked: this.data.user.ranges.eventNotificationLimit === 'needs improvement' ? true : false
        }
      ],
      buttons: [{
        text: 'Cancel',
        role: 'cancel',
        cssClass: 'secondary',
        handler: () => {
        }
      }, {
        text: 'Ok',
        handler: (data) => {
          if (data === 'no') {
            this.data.user.ranges.eventNotificationLimit = 'no notification';
            this.localDb.updateUser({
              ...this.data.user
            });
          }
          if (data === 'mediocre') {
            this.data.user.ranges.eventNotificationLimit = 'mediocre';
            this.localDb.updateUser({
              ...this.data.user
            });
          }
          if (data === 'needs') {
            this.data.user.ranges.eventNotificationLimit = 'needs improvement';
            this.localDb.updateUser({
              ...this.data.user
            });
          }
        }
      }]
    });
    await alert.present();
  }

  async presentWeeklyPricePrompt() {
    const alert = await this.alertController.create({
      header: `Notification weekly limit`,
      message: `Show  notification when total cost for week exceeds ${this.getCurrencySymbol()}${this.data.user.ranges.weeklyLimit}`,
      inputs: [{
        name: 'limit',
        type: 'number',
        value: this.data.user.ranges.weeklyLimit ? this.data.user.ranges.weeklyLimit : ''
      }],
      buttons: [{
        text: 'Cancel',
        role: 'cancel',
        cssClass: 'secondary',
        handler: () => {
        }
      }, {
        text: 'Ok',
        handler: (data) => {
          this.data.user.ranges.weeklyLimit = data.limit;
          this.localDb.updateUser({
            ...this.data.user
          });
        }
      }]
    });
    await alert.present();
  }

  async presentMonthlyPricePrompt() {
    const alert = await this.alertController.create({
      header: `Notification monthly limit`,
      message: `Show  notification when total cost for week exceeds ${this.getCurrencySymbol()}${this.data.user.ranges.monthlyLimit}`,

      inputs: [{
        name: 'limit',
        type: 'number',
        value: this.data.user.ranges.monthlyLimit ? this.data.user.ranges.monthlyLimit : ''
      }],
      buttons: [{
        text: 'Cancel',
        role: 'cancel',
        cssClass: 'secondary',
        handler: () => {
        }
      }, {
        text: 'Ok',
        handler: (data) => {
          this.data.user.ranges.monthlyLimit = data.limit;
          this.localDb.updateUser({
            ...this.data.user
          });
        }
      }]
    });
    await alert.present();
  }

}